<template>
  <!--         -->
  <div :style="styles" :class="['chart-container', cssClasses]">
    <!--  canvas    -->
    <canvas ref="canvas" :height="height" :width="width"></canvas>

    <!--     -->
    <div class="current-temperature">
      <div>{{ currentTemperature }}F</div>
    </div>

    <!--     -->
    <div class="current-time" v-if="currentTime">
      <div>{{ currentTime }}</div>
    </div>
  </div>
</template>

<script>
//    
import { Line } from 'vue-chartjs' //   Line  vue-chartjs
import Chart from 'chart.js' //   Chart.js

export default {
  name: 'LineChart', //  

  extends: Line, //   Line  vue-chartjs

  props: {
    // ID ,   'line-chart'
    chartId: {
      type: String,
      default: 'line-chart',
    },
    //       ,   'label'
    datasetIdKey: {
      type: String,
      default: 'label',
    },
    // CSS-   
    cssClasses: {
      type: String,
      default: '',
    },
    //  ,   '50px'
    height: {
      type: String,
      default: '50px',
    },
    //  ,   '75px'
    width: {
      type: String,
      default: '75px',
    },
    //    
    styles: {
      type: Object,
      default: () => ({}),
    },
    //   Chart.js,    
    plugins: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      //  
      chartData: {
        labels: [], //    X
        datasets: [
          {
            label: 'CPU Temperature', //   
            data: [], //   
            fill: false, //     
            borderColor: '#0071E2', //    
            backgroundColor: '#0071E2', //    
            pointBorderColor: '#0071E2', //   
            pointBackgroundColor: '#FFF', //   
            borderWidth: 2, //   
            pointRadius: 0, //  
            pointHoverRadius: 6, //    
          },
        ],
      },

      //   
      chartOptions: {
        responsive: true, //      
        maintainAspectRatio: false, //      
        legend: {
          display: false, //   
        },
        scales: {
          xAxes: [
            {
              type: 'time', //  X   
              time: {
                displayFormats: {
                  minute: 'h:mm a', //   
                },
                tooltipFormat: 'h:mm a', //    
                unit: 'minute', //  
              },
              gridLines: {
                display: false, //       X
              },
              ticks: {
                display: false, //      X
              },
            },
          ],
          yAxes: [
            {
              ticks: {
                display: false, //      Y
                beginAtZero: true, //    
              },
              gridLines: {
                display: false, //       Y
              },
            },
          ],
        },
        tooltips: {
          enabled: false, //  
        },
        hover: {
          mode: 'nearest',
          intersect: true,
          onHover: this.handleHover,
        },
      },

      //   
      chart: null,
      currentTemperature: 0, //  
      currentTime: '', //  
      maxPoints: 100, //     
    }
  },

  mounted() {
    //     
    this.createChart()
    //     
    this.fetchCpuTemperature()
  },

  methods: {
    //    
    createChart() {
      const ctx = this.$refs.canvas.getContext('2d') //   
      this.chart = new Chart(ctx, {
        type: 'line', //  
        data: this.chartData, //   
        options: this.chartOptions, //   
      })
    },

    //       
    fetchCpuTemperature() {
      setInterval(() => {
        const newTemperature = Math.floor(Math.random() * 100) //   
        const currentTime = new Date() //   
        const timeString = currentTime.toLocaleTimeString([], {
          hour: '2-digit',
          minute: '2-digit',
        }) //  

        //     
        this.chartData.labels.push(currentTime)
        this.chartData.datasets[0].data.push(newTemperature)
        this.currentTemperature = newTemperature
        this.currentTime = timeString

        //   ,    10
        if (this.chartData.labels.length > 10) {
          this.chartData.labels.shift()
          this.chartData.datasets[0].data.shift()
        }
        //   ,    maxPoints
        if (this.chartData.labels.length > this.maxPoints) {
          this.chartData.labels.shift()
          this.chartData.datasets[0].data.shift()
        }

        //  
        this.chart.update()
      }, 5000) //    2 
    },

    //      
    handleHover(event, activeElements) {
      if (activeElements.length > 0) {
        const pointIndex = activeElements[0]._index
        const datasetIndex = activeElements[0]._datasetIndex
        const data = this.chart.data.datasets[datasetIndex].data[pointIndex]
        const label = this.chart.data.labels[pointIndex]
        this.currentTemperature = data
        this.currentTime = label.toLocaleTimeString([], {
          hour: '2-digit',
          minute: '2-digit',
        })
      }
    },
  },
}
</script>

<style scoped lang="scss">
@function rem($px) {
  @return ($px / 16px) + rem;
}

.chart-container {
  position: relative;
  width: 70%;
  height: 100%;
}

.current-temperature {
  position: absolute;
  top: 0;
  right: -30%;
  transform: translateX(50%);
  font-size: 24px;
  font-weight: bold;
  color: #000;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.current-time {
  font-size: rem(12px);
  line-height: rem(5px);
  color: rgba(20, 18, 31, 1);
  padding: rem(8px) rem(8px);
  background: rgba(245, 245, 248, 1);
  border-radius: rem(8px);
  position: absolute;
  bottom: rem(0px);
  right: 50%;
  transform: translateX(50%);
  margin-bottom: rem(-8px);
  font-weight: 400;
}
</style>
